@import "../../colours";

.footer {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    font-size: 1rem;
    font-weight: 600;
    padding-top: 10px;
    // bottom: 0;
    background-color: var(--footer-bg);
    // height: 100px;
    // margin: 2em 10px;
    // padding: 10px 200px;

    // .footer__logo

    &__logo {
        min-height: 70px;
        max-height: 88px;
    }

    // .footer__desc

    &__desc {
        color: var(--footer-desc);
    }

    // .footer__link

    &__link {
        color: var(--footer-desc);
    }

    &__link:hover {
        color: var(--header-hover);
    }
}

